import styled from 'styled-components';
import { black, white } from './variables';

const Title = styled.h1`
  font-size: 2rem;
  color: ${white};
  margin-bottom: 2rem;
`;

const SandwichGeneratorApp = styled.main`
  margin: 0;
  padding: 0;
  padding-top: 5rem;
  background: ${black};
`;

interface GoogleMapsProps {
  name: string;
}
const GoogleMaps: React.FC<GoogleMapsProps> = props => {
  return (
    <iframe
      title='map'
      width='100%'
      height='450'
      frameBorder='0'
      style={{ border: 0 }}
      src={`https://www.google.com/maps/embed/v1/place?key=AIzaSyCc0e61vLV_PCtgOmm8eM0FUYigahvqMzU
    &q=${props.name.split(' ').join('+')}`}
      allowFullScreen
    ></iframe>
  );
};

type Place = {
  name: string;
  score: string;
  location: string;
};

interface ResultProps {
  place: Place;
}
const Result: React.FC<ResultProps> = props => {
  return (
    <Stack>
      <div>{props.place.name}</div>
      <div>{props.place.score}</div>
      <div>{props.place.location}</div>
    </Stack>
  );
};
export { Title, SandwichGeneratorApp };
